	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Упражнение по задействованным мышцам аналогично жиму штанги лежа. Отличие - основная нагрузка приходиться на нижнюю часть груди.  </p>\n<p>По сравнению с горизонтальным жимом обладает следующими преимуществами:  </p>\n<ul>\n    <li>помогает увеличить объем мышц груди работая над каждым сегментом мышцы под своим углом;</li>\n    <li>увеличение показателей в жиме лежа (классическом)/преодоление застоя;</li>\n    <li>более эффективно прокачивает грудь – нагрузка с передних головок дельт переносится на грудь, за счет этого предотвращает травму плеч;</li>\n</ul>\n<h3>Порядок выполнения:</h3>\n<ol>\n    <li>Лягте на пол. Примите упор лежа, удерживая тело на выпрямленных руках.</li>\n    <li>Исходное положение – руки немного шире плеч, ладони на одном уровне с плечами.</li>\n    <li>На вдохе - сохраняя тело прямым, сгибайте руки, пока грудью не коснетесь пола.</li>\n    <li>На выдохе – вернитесь в исходное положение.</li>\n    <li>Повторите нужное число раз.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Если Вы недавно занимаетесь в тренажерном зале, то обязательно просите подстраховать, как вариант, выполняйте упражнение в машине Смита</li>\n  <li>Старайтесь, чтобы штанга двигалась только в вертикальной траектории (вверх-вниз)</li>\n  <li>Не допускайте удара штангой в нижней точке (отбива)</li>\n  <li>следите за своими локтями, они должны быть всегда направлены в стороны.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/a6DohPfCcKA\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/jim-shtangi-golovoi-vniz","efficiency":"7","title":"Жим штанги головой вниз","image":"/static/0-06-0.jpg","image_2":"/static/0-06-1.jpg","image_3":"/static/0-06-2.jpg","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"9","bodypart":"Грудь","filtertype":"base","type":"Базовое","filterinventory":"stange","inventory":"Штанга","filterplace":""}}},"pathContext":{}}

